<the-card class="main" :frame="['head','body','foot']"><template v-slot:head-left><div class="head-left font-size-16 font-weight-500 font-color-333333">记录学时</div></template><template v-slot:head-right><div class="head-right flex flex-row"><div class="hr-btns"><a-config-provider :auto-insert-space-in-button="false"><a-button class="hrb-btn btns-radius-04 default-btns-blue" v-for="(v,k) in ['本日','本周','本月']" @click="typeSearch(k)" :type="btnType===k?'primary':'default'" :class="{'btn-color':btnType===k}">{{ ['本日','本周','本月'][k] }}</a-button></a-config-provider></div></div></template><template v-slot:body><a-spin :spinning="allLoading.timeColumn"><the-column-chart class="body" renderId="record-column-chart" :renderData="columnChartData" :renderTick="tickInterval"></the-column-chart></a-spin></template><template v-slot:foot><a-spin :spinning="allLoading.timeCircle"><div class="foot flex flex-row space-between align-items-center"><the-progress class="foot-item" v-for="(v,k) in progressData" :key="`progress-chart-${k+1}`" :renderId="`progress-chart-${k+1}`" :number="v.number" :title="v.title" :percent="v.percent" :color="v.color"></the-progress></div></a-spin></template></the-card>